import React, { useEffect, useState } from 'react';

import VoteModalTips from "./votemodalTips";
import api from "../../api";
import { useWeb3 } from "../../api/contracts";
import VotingImg from '../../img/votingImg.png';
import PublicJS from "../../utils/publicJs";
import styled from 'styled-components';
import { ethers } from "ethers"


const BtnSpan = styled.span`
    padding:0 0.5rem;
    background: #EEF5F0;
    border-radius: 0.4rem;
    border: 0.1rem solid #6AB861;
    display: inline-block;
    text-align: center;
    line-height: 2.6rem;
    font-size: 1.2rem;
    font-family: Brandon;
    font-weight: 400;
    color: #6AB861;
    img{
        display: inline-block;
        width: 1.5rem;
        margin-right: 0.5rem;
    }
`


export default function VotePending(props) {
    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, votecontract, daoOrg } = state;

    const [showModal, setShowModal] = useState(false);
    const [list, setList] = useState([]);
    const [selectid, setselectid] = useState('');
    const [withdrawId, setWithdrawId] = useState('');
    const [toTop, settoTop] = useState(false);


    useEffect(() => {
        if (props.list.length) {
            setList(props.list)
        }

    }, [props]);

    const triggerConfirm = async (id, wid) => {
        setselectid(id);
        setWithdrawId(wid)
        setShowModal(true)
    }
    const handleClose = () => {
        setShowModal(false);
        setselectid('')
    }
    const handleConfirm = async (e) => {
        setShowModal(false);
        let { id, owner } = props;

        dispatch({ type: 'LOADINGTYPE', payload: 'Confirming' });

        // const canExcute = await votecontract.canExecute(selectid)
        // console.log('** canExcute **', canExcute)

        const signer = web3Instance.getSigner(Account)
        try {
            if (typeof withdrawId === 'number' && !isNaN(withdrawId)) {
                await api.vote.executeWithdrawVote(daoOrg, signer, selectid)
            } else {
                await api.vote.executeVote(daoOrg, signer, selectid)
            }
            dispatch({ type: 'BALANCE', payload: true });
            // settoTop(data);
            props.history.push(`/voteOverview/${id}/${selectid}`)
        } catch (error) {
            console.error(error)

            dispatch({ type: 'MSGTYPE', payload: { msg: `${error.data.message}` } });
        } finally {
            dispatch({ type: 'LOADINGTYPE', payload: null });
        }
    }
    const handleClicktoVoteview = (voteid) => {
        let { id, owner } = props;
        props.history.push(`/voteOverview/${id}/${voteid}`)
    }
    return (<div className='votePending'>
        <VoteModalTips
            handleClose={handleClose}
            handleConfirm={handleConfirm}
            showTips={showModal} />
        <table className="myTable votePending">
            <thead>
                <tr>
                    <th>Number</th>
                    <th>Time</th>
                    <th>Title</th>
                    <th>Action</th>
                </tr>
            </thead>
            <tbody>
                {
                    list.map((item, index) => <tr key={`Pending_${item.vote_id}`}>
                        <td onClick={() => handleClicktoVoteview(item.vote_id)}>{index + 1}</td>
                        <td onClick={() => handleClicktoVoteview(item.vote_id)}>{item.endDisplay}</td>
                        <td onClick={() => handleClicktoVoteview(item.vote_id)}>{item.title}</td>
                        <td>
                            <BtnSpan  onClick={() => triggerConfirm(item.vote_id, item.wid)}><img src={VotingImg} alt=""/>Proceed</BtnSpan>
                        </td>
                    </tr>)
                }

            </tbody>
        </table>
    </div>)
}

